import { AuthenticationData } from '@strife/common';
import { UserServiceId } from './user.interface';
import type { IUser } from './user.interface';

@injectable()
export default class User implements IUser {

  private _authentication: AuthenticationData;
  public get authentication(): AuthenticationData {
    return this._authentication;
  }

  public constructor(authentication: AuthenticationData) {
    this._authentication = authentication;
  }

}

di.bind(AccountServiceServiceId).to(AccountService).inSingletonScope();